
.popup-box {
  position: fixed;
  background: #00000050;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 100;
  transition: all 1s linear forwards;
}
 
.box {
  position: relative;
  width: 60%;
  margin: 0px auto 25px auto;
  height: auto;
  max-height: 70vh;
  margin-top: calc(100vh - 75vh);
  background: #fff;
  border-radius: 4px;
  padding: 20px;
  border: 1px solid #999;
  text-align: center;
  transform: translateY(-20px);
  transition: all 1s linear forwards;
  overflow: auto !important;
}

*.unselectable {
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}