body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

iframe
{
    display: none;
}

.onDragOver {
    height: 100%; 
    width: 100%;
    cursor: pointer; 
    pointer-events: none;
    background:rgba(128,128,128, 0.1);
    -webkit-animation: changeBackgroundColor 0.15s ease-in-out forwards;
            animation: changeBackgroundColor 0.15s ease-in-out forwards;
}

.onDragOut {
    height: 100%;
    -webkit-animation: revertBackgroundColor 0.15s ease-in-out forwards;
            animation: revertBackgroundColor 0.15s ease-in-out forwards;
}

@-webkit-keyframes changeBackgroundColor {
    from {
        background: rgba(128,128,128, 0.1);
    } to {
        background: rgba(128,128,128, 0.3);
    }
}

@keyframes changeBackgroundColor {
    from {
        background: rgba(128,128,128, 0.1);
    } to {
        background: rgba(128,128,128, 0.3);
    }
}

@-webkit-keyframes revertBackgroundColor {
    from {
        background: rgba(128,128,128, 0.3);
    } to {
        background: rgba(128,128,128, 0.1);
    }
}

@keyframes revertBackgroundColor {
    from {
        background: rgba(128,128,128, 0.3);
    } to {
        background: rgba(128,128,128, 0.1);
    }
}

