  --bs-btn-hover-color: #fff;
  --bs-btn-hover-bg: #020261 !important;
  --bs-btn-hover-border-color: #020261 !important;
  --bs-btn-active-color: #fff;
  --bs-btn-active-bg: #020261 !important;
  --bs-btn-active-border-color: #020261 !important;
  --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
  --bs-btn-disabled-color: #fff;
  --bs-btn-disabled-bg: #000099 !important;
  --bs-btn-disabled-border-color: #000099 !important;
}

.btn-check:focus + .btn, .btn:focus {
  color: #fff !important;
